.owner-index{
    display: grid;
    grid-template-columns: 2fr 8fr;
    @include telefono(){
        display: flex;
        flex-direction: column;
    }
    .citas{
        width: 90%;
        margin: auto;
        overflow-y: scroll;
        height: 70vh;
        grid-column: 2 / 3;
        .appointment-confirmed{
            text-align: center;
            margin: 2rem 0;
            @include grid(5, 1rem);
            border-bottom: 2px solid $principal;
            p{
                margin: 0;
                height: fit-content;
            }
        }
        .services-appointment{
            p{
                text-align: left;
                margin-top: 0.1rem;
            }
            display: flex;
            flex-direction: column;
            gap: 1rem;
            justify-content: space-between;
        }
        .date-time-appointment{
            p{
                text-align: left;
                margin-top: 0.1rem;
            }
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }
        .status-appointment,
        .cancel-appointment{
            display: grid;
            place-items: center;
            height: min-content;
            button{
                margin: 0 0 1rem 0;
            }
        }
    }
    .title{
        grid-row: 1 / 3;
    }
    @include telefono(){
        .citas{
            .appointment-confirmed{
                display: flex;
                flex-direction: column;
                p{
                    text-align: start;
                }
                .date-time-appointment{
                    @include grid(2, 1rem);
                    p{
                        text-align: center;
                    }
                }
                .cancel-appointment{
                    margin-bottom: 2rem;
                }
            }
        }
    }
    .title-appointment{
        width: 90%;
        margin: 2rem auto auto auto;
        @include grid(5, 1rem);
        h1{
            font-size: 2.5rem;
            color: $principal;
            margin: 0;
        }
        @include telefono(){
            display: none;
        }
    }
}