.reservation{
    display: grid;
    grid-template-columns: 2fr 8fr;
    .calendar-time{
        display: grid;
        grid-template-columns: 6fr 4fr;
        .ca{
            margin: auto;
            width: 90%;
            .volver-img{
                margin: 0 0 1% 5%;
            }
        }
        .calendar{
            padding: 5rem 0;
            @include degraded-background($principal, $terciario);
            margin: auto;
            width: 90%;
            border-radius: 2rem;
            .calendar-info{
                display: flex;
                place-items: center;
                justify-content: center;
                margin: 1rem 3rem;
            }
            .prev{
                margin-right: auto;
                cursor: pointer;
                filter: invert(100%);
            }
            .month,
            .year{
                margin: 1rem;
                color: white;
                font-weight: bold;
                font-size: 2.1rem;
            }
            .next{
                margin-left: auto;
                cursor: pointer;
                filter: invert(100%);
            }
        }
        .calendar-week,
        .calendar-dates{
            margin: 1rem 2rem;
            @include grid(7, 1rem);
            .day{
                text-align: center;
                color: white;
                font-weight: 600;
            }
        }
        .calendar-dates{
            height: 50%;
            .item{
                color: $gris;
                text-align: center;
            }
            .today{
                background-color: aqua;
                border-radius: 50%;
            }
            .date{
                cursor: pointer;
            }
            .color-darken{
                color: $grisOscuro;
            }
        }
        .btn-next{
            grid-column: 2 / 4;
            display: flex;
            justify-content: flex-end;
            margin-right: 20%;
            margin-bottom: 2rem;
            align-items: center;
            button{
                height: max-content;
                padding: 1rem 2rem;
            }
        }
    }
    .time{
        margin: auto;
        p{
            font-weight: bold;
            margin-bottom: 10rem;
        }
    }
    .model-time{
        background-color: $gris;
        border-radius: 2rem;
    }
    .date-saved{
        text-align: center;
        margin-bottom: 3rem;
        h1{
            padding-top: 2rem;
            color: $principal;
            font-size: 2.4rem;
            margin: 0;
        }
        h4{
            margin: 0;
            font-size: 1.7rem;
        }
    }
    .select-time{
        padding: 1rem;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        .input-time{
            display: flex;
            flex-direction: column;
            input{
                width: 80%;
                margin: auto;
            }
        }
        button{
            margin: 0;
        }
    }
    .back-button{
        margin: 2rem 0;
    }
}
.show-summary{
    height: 100vh;
    display: grid;
    grid-template-columns: 2fr 8fr;
    .summary{
        height: 100vh;
    }
    .container-show-summary{
        height: 53%;
        overflow-y: scroll;
    }
    .titles{
        display: grid;
        grid-template-columns: 4fr 2fr 2fr 2fr;
        width: 90%;
        margin: auto;
        h1{
            margin: 2rem 0;
            font-size: 2.4rem;
        }
    }
    .container-summary{
        display: grid;
        grid-template-columns: 4fr 2fr 2fr 2fr;
        border-bottom: 1px solid $principal;
        width: 90%;
        margin: auto;
        padding: 1rem 0;
    }
    .product-service{
        display: grid;
        grid-template-columns: 2fr 2fr 6fr;
        .cancel{
            justify-self: center;
            align-self: center;
            width: 30%;
            cursor: pointer;
        }
        .product{
            justify-self: center;
            align-self: center;
            width: 90%;
            height: 90%;
        }
        .description{
            margin-left: 1.5rem;
            text-align: left;
            p{
                margin: 1rem 0;
            }
            span{
                color: darken($gris, 40);
            }
            .description-service{
                display: flex;
                justify-content: space-between;
                span{
                    color: $principal;
                }
            }
        }
    }
    .price, 
    .quantity,
    .subtotal{
        align-self: center;
        text-align: center;
        color: $principal;
        font-weight: bold;
        font-size: 1.5rem;
        p{
            font-size: 1.5rem;
        }
    }
    .result{
        display: flex;
        flex-direction: column;
        margin-top: 4%;
        margin-left: 50%;
        margin-right: 5%;
        .model-result{
            background-color: $gris;
            border-radius: 1.5rem;
            margin-bottom: 1rem;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            padding: 1rem 0;
            p{
                margin: 0 0 0rem 2rem;
                color: $principal;
                font-weight: bold;
            }
            .model-time,
            .model-price-total{
                background-color: white;
                text-align: center;
                border-radius: 1rem;
                width: 15%;
                margin-right: 1rem;
                span{
                    font-size: 1.3rem;
                    color: $principal;
                    font-weight: bold;
                }
            }
        }
    }
    .buttons{
        display: flex;
        justify-content: space-between;
        margin: 0 5%;
        button{
            align-self: flex-end;
            border-radius: 1.3rem;
            height: max-content;
            margin: 0;
        }
    }
}
@include telefono(){
    .reservation{
        display: flex;
        flex-direction: column;
        @include title-phone();
        .calendar-time{
            display: flex;
            flex-direction: column;
            .ca{
                .volver-img{
                    margin-bottom: 2rem;
                }
                height: 50vh;
            }
            .time{
                p{
                    margin: 0;
                    padding-top: 3rem;
                }
                height: 30vh;
            }
            .btn-next{
                height: 10vh;
                margin: 0;
                button{
                    margin: 0 3rem 0 0;
                }
            }
        }
    }
    .show-summary{
        display: flex;
        flex-direction: column;
        @include title-phone();
        .summary{
            height: 85vh;
            .titles{
                display: none;
            }
        }
        .container-show-summary{
            height: 57vh;
            width: 97%;
            margin: auto;
            .container-summary{
                grid-template-columns: 6fr 1.3fr 1.3fr 1.3fr;
                .product-service{
                    .cancel{
                        width: 50%;
                    }
                }
                .description{
                    p{
                        font-size: 1.6rem;
                    }
                }
            }
        }
        .result{
            margin-top: 5vh;
            height: 20vh;
            .model-result{
                .model-time{
                    width: 30%;
                }
                .model-price-total{
                    width: 30%;
                }
            }
        }
        .buttons{
            height: 5vh;
            button{
                margin-bottom: 5vh;
            }
        }
    }
}