.container {
    margin: auto;
    width: 80%;
}
.container-small {
    margin: auto;
    width: 90%;
    @include tablet{
        width: 75%;
    }
    @include desktop{
        width: 50%;
    }
}
.center{
    text-align: center;
}

.text-center {
    text-align: center;
}

.text-justify{
    text-align: justify;
    margin: 2rem;
}

.text-white{
    color: white;
}

.text-black{
    color: black;
}

.text-primary{
    color: $principal;
}

.width-100{
    width: 100%;
}

.btn-principal {
    font-weight: bold;
    border: none;
    background-color: $principal;
    border-radius: 0.5rem;
    padding: 1rem 2rem;
    color: $color1;
    margin-bottom: 2rem;
}

.btn-principal:hover {
    cursor: pointer;
    background-color: darken($principal, 8);
}

.btn-secundario {
    font-weight: bold;
    border: none;
    background-color: $blanco;
    border-radius: 0.5rem;
    padding: 0.5rem 0;
    color: $principal;
    padding: 1rem 2rem;
}

.btn-secundario:hover {
    cursor: pointer;
    background-color: darken($blanco, 8);
}
.btn-terciario {
    font-weight: bold;
    border: none;
    background-color: $gris;
    border-radius: 0.5rem;
    padding: 1rem 2rem;
    color: $principal;
    margin-bottom: 2rem;
}

.btn-terciario:hover {
    cursor: pointer;
    background-color: darken($gris, 8);
}

.error {
    background-color: lighten($rojo, 47);
    border-left: 5px solid $rojo;
    text-align: center;
    padding: 0.2rem 0;
    margin: 1rem 2rem;
    font-weight: $bold;
}

.successful {
    background-color: lighten($verde, 47);
    border-left: 5px solid $verde;
    padding: 0.2rem 0;
    text-align: center;
    margin: 1rem 2rem;
    font-weight: $bold;
}

.btn-volver,
.btn-cerrar-sesion{
    border: none;
    background-color: $gris;
    border-radius: 0.5rem;
    padding: 0.5rem 1rem;
}
.btn-volver:hover,
.btn-cerrar-sesion:hover {
    cursor: pointer;
    background-color: darken($gris, 10);
}

.div-btn-volver{
    margin: 1rem;
}

.animacion-menu{
    display: flex;
    align-items: center;
    img{
        filter: invert(1);
        width: 4rem;
    }
    @include tablet{
        margin-right: 5rem;
        display: flex;
        justify-content: flex-end;
        img{
            filter: invert(1);
            width: 4rem;
        }
    }
    @include desktop{
        margin-right: 5rem;
        display: flex;
        justify-content: flex-end;
        img{
            filter: invert(1);
            width: 4rem;
        }
    }
    .acciones{
        display: none;
    }
}
.animacion-menu:hover{
    cursor: pointer;
}
.mostrar{
    display: block;
}

.solo-icono{
    margin: 1rem 2rem 0rem 2rem;
    a{
        width: 5rem;
        img{
            width: 5rem;
        }
    }
    button{
        width: 5rem;
        background-color: transparent;
    }
}

.opciones-menu{
    margin-top: 2rem;
}

.opciones-menu:first-child{
    margin: 0;
}

