.no-mostrar{
    display: none;
}
.ocultar{
    visibility: hidden;
}
.icon-back{
    margin: 2rem 2rem;
    width: 10%;
}

.icon-back:hover{
    cursor: pointer;
}

.btn-back{
    border: none;
    background-color: transparent;
}
.btn-back:hover{
    cursor: pointer;
}

.btn-duals{
    @include grid(2,1rem);
}

.btn-option{
    margin-bottom: 2rem;
}

.swal2-popup {
    background: #ffffff;
}

.swal2-title {
  color:#000000;
}

swal2-styled.swal2-confirm {
    background-color:#5d14a2
}

.title{
    color: white;
    .title-p{
        top: 50%;
        position: absolute;
        text-align: center;
        width: 20%;
    }
    background-color: $terciario;
    img{ 
        position: relative;
        height: 100vh;
        width: 100%;
        filter: opacity(60%);
    }
}
.field{
    text-align: left;
}

