.shortcuts{
    @include telefono() {
        display: grid;
        grid-template-rows: repeat(5, 1fr);
        margin: 0 auto;
        top: 50%;
    }
    padding: 0rem 2rem;
    display: flex;
    justify-content: space-between;
    .btn-shortcut{
        margin: auto;
        height: 2rem;
        border: none;
        background-color: transparent;
        font-size: 1.7rem;
        font-weight: bold;
    }
    .btn-shortcut:hover{
        cursor: pointer;
        color: $principal;
    }
}

.btn-remove,
.btn-status-pending,
.btn-status-success,
.btn-review{
    font-weight: bold;
    border: none;
    border-radius: 0.5rem;
    padding: 1rem 2rem;
    color: $color1;
    margin-bottom: 2rem;
}
.btn-remove{
    background-color: red;
}
.btn-status-pending{
    background-color: $principal;
}
.btn-status-success{
    background-color: green;
}
.btn-review{
    background-color: blue;
}
.btn-review:hover{
    cursor: pointer;
    background-color: darken($color: blue, $amount: 13);

}
.btn-remove:hover{
    cursor: pointer;
    background-color: darken($color: red, $amount: 13);
}
.btn-update{
    font-weight: bold;
    border: none;
    background-color: orange;
    border-radius: 0.5rem;
    padding: 1rem 2rem;
    color: $color1;
    margin-bottom: 2rem;
}
.btn-update:hover{
    cursor: pointer;
    background-color: darken($color: orange, $amount: 13);
}
.selected{
    @include degraded-background($principal, $secundario);
}
.modal-overlay{
    height: 100%;
    width: 100%;
    display: grid;
    place-items: center;
    background-color: rgb(0, 0, 0, 50%);
    position: fixed;
    top: 0;
    .modal-form{
        width: 40%;
        margin: auto;
        background-color: white;
        border-radius: 2rem;
        h1{
            color: $principal;
        }
        form{
            width: 70%;
            margin: auto;
            padding: 2rem 0;
            display: flex;
            flex-direction: column;
            label{
                text-align: left;
            }
            input{
                width: 100%;
                border-radius: 1rem;
                align-self: center;
                border-color: $terciario;
                margin-bottom: 2rem;
            }
            textarea{
                width: 100%;
                border-color: $terciario;
                border-radius: 1rem;
                margin-bottom: 2rem;
                resize: none;
                height: 9vh;
            }
        }
    }
}
.volver-img{
    width: 4rem;
    margin: 3rem 0 0 3rem;
    button{
        background-color: transparent;
        border: none;
        cursor: pointer;
    }
}
.select-branch-owner{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin-left: 5%;
    margin-top: 2rem;
    margin-bottom: 4rem;
    p{
        margin: 0;
    }
    select{
        margin-left: 1rem;
        height: min-content;
        border-color: $principal;
        background-color: white;
        border-radius: 2rem;
        text-align: center;
    }
}
@include telefono(){
    .no-mostrar{
        display: none !important;
    }
    .mostrar{
        display: block !important;
    }
    .modal-overlay{
        height: 100%;
        width: 100%;
        display: grid;
        place-items: center;
        background-color: rgb(0, 0, 0, 50%);
        position: fixed;
        top: 0;
        .modal-form{
            width: 90%;
            margin: auto;
            background-color: white;
            border-radius: 2rem;
            form{
                width: 70%;
                textarea{
                    height: 15vh;
                }
            }
        }
    }
}