.appointment{
    display: grid;
    grid-template-columns: 2fr 8fr;
    .filter-appintment-purchase{
        display: flex;
        flex-direction: row;
        width: 30%;
        margin: auto;
        justify-content: space-between;
        input[type="radio"]{
            accent-color: $principal;
            margin-right: 1rem;
        }
        label{
            font-size: 2rem;
        }
    }
    .scroll-appointment{
        height: 85vh;
        overflow-y: scroll;
    }
    .appointment-container{
        display: grid;
        grid-template-columns: 6fr 2fr;
        margin: 2rem;
        width: 96%;
        height: max-content;
        background-color: $grisClaro;
        border-radius: 1rem;
        .info-appointment{
            margin: 1rem;
            @include grid(2, 1rem);
            p{
                margin: 1rem 0rem;
                font-weight: bold;
            }
            span{
                font-weight: 400;
                color: $principal;
            }
            .products-services-appointment{
                grid-column: 1 / 3;
                .services-appointment,
                .products-appointment{
                    @include grid(4, 2rem);
                    span{
                        margin-left: 3rem;
                    }
                }
            }
        }
        
    }
    .actions-appointment{
        display: flex;
        flex-direction: column;
        margin: 2rem;
        h4{
            text-align: center;
        }
        button{
            margin: 1rem 0;
        }
    }
}
@include telefono(){
    .appointment{
        .volver-img{
            margin-bottom: 2rem;
            margin-left: 1rem;
        }
        .filter-appintment-purchase{
            display: flex;
            width: 90%;
            justify-content: space-between;
            align-items: center;
            margin: auto auto 1rem auto;
            input[type="radio"]{
                margin-right: 0.5rem;
            }
        }
        display: flex;
        flex-direction: column;
        @include title-phone();
        .scroll-appointment{
            height: 80vh;
            overflow-y: scroll;
        }
        .appointment-container{
            display: flex;
            flex-direction: column;
            width: 98%;
            margin: 0 auto 2rem auto;
            .info-appointment{
                width: 98%;
                margin: auto;
                .products-services-appointment{
                    display: grid;
                    grid-template-columns: 2fr 8fr;
                    row-gap: 2rem;
                    p{
                        place-self: center;
                    }
                    .services-appointment,
                    .products-appointment{
                        text-align: center;
                        @include grid(3, 1rem);
                        span{
                            margin: 0;
                            place-self: center;
                        }
                    }
                }
            }
            .actions-appointment{
                h4{
                    display: none;
                }
            }
        }
    }
}