.navegation-phone,
.user-interaction-phone,
.menu-phone{
    display: none;
}
.nav-selection{
    width: 95%;
    display: grid;
    grid-template-columns: 2fr 5fr 3fr;
    .logo-nav{
        margin: 0 auto;
        width: 50%;
    }
    .user-interaction{
        display: flex;
        justify-content: space-between;
        .user-animation{
            margin: auto;
            width: 15%;
            button{
                border: none;
                background-color: transparent;
            }
        }
        .options-animation{
            align-self: center;
            margin: auto;
            width: 10%;
            p{
                font-size: 1.7rem;
            }
            .window-options{
                width: 50%;
                background-color: white;
                border-radius: 1rem;
                padding: 1rem;
                img{
                    max-width: 30%;
                }
                .option-menu{
                    text-align: center;
                    border-radius: 0.5rem;
                    // width: 40%;
                }
                .option-menu:hover{
                    cursor: pointer;
                }
            }
        }
        .options-animation:hover{
            cursor: pointer;
        }
    }
    .actions{
        display: none;
    }
    .menu-buttons{
        position: absolute;
        z-index: 100;
    }
    .btn-logout{
        border: none;
        background-color: transparent;
        button{
            width: 50%;
        }
    }
    .btn-logout:hover{
        cursor: pointer;
    }
}
@include telefono(){
    .logo-nav,
    .shortcuts,
    .title-appointment,
    .user-animation,
    .shortcuts,
    .options-animation{
        display: none;
    }
    .nav-selection{
        display: flex;
        flex-direction: column;
        .menu-phone{
            display: block;
            margin: 1rem auto;
            .img-phone{
                display: grid;
                place-items: center;
                width: 12%;
                margin: auto;
            }
        }
        .shortcuts-menuPhone{
            display: grid;
            grid-template-rows: repeat(5, 1fr);
            margin: 0.5rem auto;
            .btn-shortcut-menuPhone{
                margin: 0.5rem auto;
                height: 2rem;
                border: none;
                background-color: transparent;
                font-size: 1.7rem;
                font-weight: bold;
            }
            .btn-shortcut-menuPhone:hover{
                cursor: pointer;
                color: $principal;
            }
        }
    }
    @include title-phone();
    .user-interaction-phone{
        margin-top: 2rem;
        @include grid(2, 1fr);
        .user-animation-phone{
            button{
                border: none;
                background-color: transparent;
            }
            img{
                width: 25%;
            }
            p{
                margin: 0;
                font-size: 1.4rem;
            }
        }
        .option-menu-phone{
            text-align: center;
            img{
                width: 23%
            }
            p{
                margin: 0;
                font-size: 1.4rem;
            }
        }
    }
}