.dashboard{
    width: 90%;
    margin: auto;
    .volver-img{
        margin: 0 0 1% 1%;
        img{
            width: 120%
        }
    }
    .windows{
        @include grid(4, 1rem);
    }
    .logo-dashboard{
        margin: 0 auto;
        width: 50%;
    }
    .option-client{
        border-radius: 1rem;
        height: 68vh;
        background-color: $principal;
        position: relative;
        .title-dashboard{
            color: azure;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
        img{
            border-radius: 1rem;
            position: relative;
            height: 68vh;
            filter: opacity(60%);
        }
    }
    .option-client:hover{
        cursor: pointer;
    }
}
@include telefono(){
    .nav-selection{
        .dashboard-Phone{
            .shortcuts-menuPhone{
                display: flex;
                margin: 0.5rem auto;
            }
        }
    }
    .dashboard{
        .volver-img{
            display: none;
        }
        .windows{
            display: flex;
            flex-direction: column;
            .option-client{
                height: 20vh;
                img{
                    width: 100vh;
                    height: 20vh;
                }
            }
        }
    }
}