h1,
h2,
h3 {
  font-family: "Courgette", cursive;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 2rem;
}

label {
  font-size: "Ubuntu", sans-serif;
}

.container {
  margin: auto;
  width: 80%;
}

.container-small {
  margin: auto;
  width: 90%;
}
@media (max-width: 600px) {
  .container-small {
    width: 75%;
  }
}
@media (max-width: 1024px) {
  .container-small {
    width: 50%;
  }
}

.center {
  text-align: center;
}

.text-center {
  text-align: center;
}

.text-justify {
  text-align: justify;
  margin: 2rem;
}

.text-white {
  color: white;
}

.text-black {
  color: black;
}

.text-primary {
  color: #7218C6;
}

.width-100 {
  width: 100%;
}

.btn-principal {
  font-weight: bold;
  border: none;
  background-color: #7218C6;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  color: #F2E9E4;
  margin-bottom: 2rem;
}

.btn-principal:hover {
  cursor: pointer;
  background-color: #5d14a2;
}

.btn-secundario {
  font-weight: bold;
  border: none;
  background-color: #FFFFFF;
  border-radius: 0.5rem;
  padding: 0.5rem 0;
  color: #7218C6;
  padding: 1rem 2rem;
}

.btn-secundario:hover {
  cursor: pointer;
  background-color: #ebebeb;
}

.btn-terciario {
  font-weight: bold;
  border: none;
  background-color: #dcdcdc;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  color: #7218C6;
  margin-bottom: 2rem;
}

.btn-terciario:hover {
  cursor: pointer;
  background-color: #c8c8c8;
}

.error {
  background-color: #ffbcbc;
  border-left: 5px solid #cb0000;
  text-align: center;
  padding: 0.2rem 0;
  margin: 1rem 2rem;
  font-weight: 700;
}

.successful {
  background-color: #b3ff90;
  border-left: 5px solid #329f00;
  padding: 0.2rem 0;
  text-align: center;
  margin: 1rem 2rem;
  font-weight: 700;
}

.btn-volver,
.btn-cerrar-sesion {
  border: none;
  background-color: #dcdcdc;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
}

.btn-volver:hover,
.btn-cerrar-sesion:hover {
  cursor: pointer;
  background-color: #c3c3c3;
}

.div-btn-volver {
  margin: 1rem;
}

.animacion-menu {
  display: flex;
  align-items: center;
}
.animacion-menu img {
  filter: invert(1);
  width: 4rem;
}
@media (max-width: 600px) {
  .animacion-menu {
    margin-right: 5rem;
    display: flex;
    justify-content: flex-end;
  }
  .animacion-menu img {
    filter: invert(1);
    width: 4rem;
  }
}
@media (max-width: 1024px) {
  .animacion-menu {
    margin-right: 5rem;
    display: flex;
    justify-content: flex-end;
  }
  .animacion-menu img {
    filter: invert(1);
    width: 4rem;
  }
}
.animacion-menu .acciones {
  display: none;
}

.animacion-menu:hover {
  cursor: pointer;
}

.mostrar {
  display: block;
}

.solo-icono {
  margin: 1rem 2rem 0rem 2rem;
}
.solo-icono a {
  width: 5rem;
}
.solo-icono a img {
  width: 5rem;
}
.solo-icono button {
  width: 5rem;
  background-color: transparent;
}

.opciones-menu {
  margin-top: 2rem;
}

.opciones-menu:first-child {
  margin: 0;
}

.login-img:hover {
  cursor: pointer;
}

.no-mostrar {
  display: none;
}

.ocultar {
  visibility: hidden;
}

.icon-back {
  margin: 2rem 2rem;
  width: 10%;
}

.icon-back:hover {
  cursor: pointer;
}

.btn-back {
  border: none;
  background-color: transparent;
}

.btn-back:hover {
  cursor: pointer;
}

.btn-duals {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}

.btn-option {
  margin-bottom: 2rem;
}

.swal2-popup {
  background: #ffffff;
}

.swal2-title {
  color: #000000;
}

swal2-styled.swal2-confirm {
  background-color: #5d14a2;
}

.title {
  color: white;
  background-color: #C933C7;
}
.title .title-p {
  top: 50%;
  position: absolute;
  text-align: center;
  width: 20%;
}
.title img {
  position: relative;
  height: 100vh;
  width: 100%;
  filter: opacity(60%);
}

.field {
  text-align: left;
}

.shortcuts {
  padding: 0rem 2rem;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 480px) {
  .shortcuts {
    display: grid;
    grid-template-rows: repeat(5, 1fr);
    margin: 0 auto;
    top: 50%;
  }
}
.shortcuts .btn-shortcut {
  margin: auto;
  height: 2rem;
  border: none;
  background-color: transparent;
  font-size: 1.7rem;
  font-weight: bold;
}
.shortcuts .btn-shortcut:hover {
  cursor: pointer;
  color: #7218C6;
}

.btn-remove,
.btn-status-pending,
.btn-status-success,
.btn-review {
  font-weight: bold;
  border: none;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  color: #F2E9E4;
  margin-bottom: 2rem;
}

.btn-remove {
  background-color: red;
}

.btn-status-pending {
  background-color: #7218C6;
}

.btn-status-success {
  background-color: green;
}

.btn-review {
  background-color: blue;
}

.btn-review:hover {
  cursor: pointer;
  background-color: #0000bd;
}

.btn-remove:hover {
  cursor: pointer;
  background-color: #bd0000;
}

.btn-update {
  font-weight: bold;
  border: none;
  background-color: orange;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  color: #F2E9E4;
  margin-bottom: 2rem;
}

.btn-update:hover {
  cursor: pointer;
  background-color: #bd7a00;
}

.selected {
  background: linear-gradient(#7218C6, #9623C8);
}

.modal-overlay {
  height: 100%;
  width: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
}
.modal-overlay .modal-form {
  width: 40%;
  margin: auto;
  background-color: white;
  border-radius: 2rem;
}
.modal-overlay .modal-form h1 {
  color: #7218C6;
}
.modal-overlay .modal-form form {
  width: 70%;
  margin: auto;
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
}
.modal-overlay .modal-form form label {
  text-align: left;
}
.modal-overlay .modal-form form input {
  width: 100%;
  border-radius: 1rem;
  align-self: center;
  border-color: #C933C7;
  margin-bottom: 2rem;
}
.modal-overlay .modal-form form textarea {
  width: 100%;
  border-color: #C933C7;
  border-radius: 1rem;
  margin-bottom: 2rem;
  resize: none;
  height: 9vh;
}

.volver-img {
  width: 4rem;
  margin: 3rem 0 0 3rem;
}
.volver-img button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.select-branch-owner {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-left: 5%;
  margin-top: 2rem;
  margin-bottom: 4rem;
}
.select-branch-owner p {
  margin: 0;
}
.select-branch-owner select {
  margin-left: 1rem;
  height: min-content;
  border-color: #7218C6;
  background-color: white;
  border-radius: 2rem;
  text-align: center;
}

@media (max-width: 480px) {
  .no-mostrar {
    display: none !important;
  }
  .mostrar {
    display: block !important;
  }
  .modal-overlay {
    height: 100%;
    width: 100%;
    display: grid;
    place-items: center;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
  }
  .modal-overlay .modal-form {
    width: 90%;
    margin: auto;
    background-color: white;
    border-radius: 2rem;
  }
  .modal-overlay .modal-form form {
    width: 70%;
  }
  .modal-overlay .modal-form form textarea {
