    font-size: 2.7rem;
  }
}
.seleccion-sucursal .info {
  margin: 0rem 2rem;
}
.seleccion-sucursal .info span {
  color: #B4D2D9;
}
.seleccion-sucursal .info form {
  display: flex;
  justify-content: flex-end;
}
.seleccion-sucursal .mapa {
  height: 30rem;
}
@media (max-width: 600px) {
  .seleccion-sucursal .mapa {
    height: 40rem;
    grid-row: 1/3;
    grid-column: 2/3;
  }
}
@media (max-width: 1024px) {
  .seleccion-sucursal .mapa {
    height: 40rem;
    grid-row: 1/3;
    grid-column: 2/3;
    margin: 5% 5%;
  }
}

.seleccion-cliente-encargada {
  margin-top: 10rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  text-align: center;
}

.text-informativo {
  margin-top: 10rem;
}
.text-informativo p {
  font-size: 2.4rem;
}

.navegacion-inicio {
  border: none;
  padding: 1rem 0;
  background-color: #013440;
}
@media (max-width: 600px) {
  .navegacion-inicio {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.seleccion-sucursal {
  display: block;
}

.seleccion-no-sucursal {
  display: none;
}

.nombre-usuario {
  margin: auto;
  font-size: 2.4rem;
  color: #F2E9E4;
}

.log-out {
  display: none;
}

.login {
  display: block;
}

.sidebar-admin {
  margin-right: 85%;
  position: absolute;
  width: 15%;
  background-color: #013440;
  height: 100%;
}
.sidebar-admin h1 {
  padding-top: 2rem;
  color: #F2E9E4;
}

.sidebar-admin-btn {
  margin: 1.5rem 2rem;
}
.sidebar-admin-btn button {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.cerrar-sesion {
  margin-left: 2rem;
  display: flex;
  flex-direction: row;
}

.container-admin {
  margin: 0 5rem;
  padding-top: 2rem;
}

.filtro-color {
  filter: invert(1);
}

.body-admin {
  width: 85%;
  margin-left: 15%;
