.services{
    display: grid;
    grid-template-columns: 2fr 6fr 2fr;
    height: 100vh;
}
.time-cost-service{
    display: grid;
    place-items: center;
    .title-service{
        font-size: 3rem;
        color: $principal;
        display: grid;
        place-self: start;
    }
    .circular-time{
        position: relative;
        width: 12rem;
        height: 12rem;
        border-radius: 50%;
        position: absolute;
        background: linear-gradient(90deg, $principal 50%, $terciario 50%);
        margin: auto;
        display: grid;
        place-items: center;
    }
    .circular-time::before{
        content: "";
        width: 12rem;
        height: 12rem;
        position: absolute;
        top: 0;
        left: 0;
        background: white content-box;
        border-radius: 50%;
        padding: 0.5em;
        box-sizing: border-box;
        font-size: 2rem;
    }
    .value-time{
        position: relative;
    }
}
.price-service{
    padding-top: 17rem;
    color: $principal;
    font-size: 2rem;
    font-weight: 900;
}

.form-service{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 1rem;
    .input-service{
        display: flex;
        justify-content: center;
        margin-bottom: 2rem;
        input[type="number"]{
            -moz-appearance: textfield;
            text-align: center;
            border-color: $terciario;
            width: 10%;
        }
        input::-webkit-outer-spin-button,
        input::-webkit-inner-spin-button{
            -webkit-appearance: none;
            margin: 0;
        }
        .decrement,
        .increment{
            border: none;
            background-color: transparent;
            color: $terciario;
            font-size: 2.5rem;
            text-align: center;
            padding: 0.3rem 1rem;
        }
        .decrement:hover,
        .increment:hover{
            cursor: pointer;
        }
    } 
}
.more-services{
    background-color: black;
    color: white; 
    height: auto;
    overflow-y: scroll;
    p{
        color: white;
        font-size: 1.5rem;
        text-align: justify;
    }
    h4{
        font-weight: bold;
        font-size: 1.7rem;
    }
    .one-service{
        width: 90%;
        margin: auto;
        padding: 0.7rem;
        .description-oneservice{
            padding-right: 0.5rem;
        }
    }
    .one-service:last-child{
        margin-bottom: 1rem;
    }
    .one-service:first-child{
        margin-top: 1rem;
    }
    .one-service:hover{
        cursor: pointer;
        @include degraded-background($morado-intenso, $morado-no-intenso);
    }
}

@include telefono(){
    .services{
        display: flex;
        flex-direction: column;
        @include title-phone();
    }
    .show-service{
        height: 60vh;
        overflow-y: scroll;
    }
    .more-services{
        @include grid(1, 0rem);
        height: 30vh;
        .one-service{
            height: 10vh;
        }
    }
}