.dark-mode {
    background-color: darken($grisOscuro, 10);
    a,
    h1,
    h2,
    h3,
    h4,
    p,
    label{
        color: $gris;
    }
    .btn-shortcut{
        color: $gris;
    }
    .ventana,
    .ventana-registro{
        h1{
            color: $color5;
        }
    }
    .seleccionar-fecha{
        color: $color1;
    }
    .window-options,
    .window-login,
    .modal-form{
        background-color: darken($grisOscuro, 10);
    }
    .appointment-container{
        p,
        h4{
            color: black;
        }
    }
    .filter{
        filter: invert(100%);
    }
}