.owner-services{
    display: grid;
    grid-template-columns: 2fr 8fr;
    .title-services{
        width: 90%;
        margin: auto;
        @include grid(4, 1rem);
        h1{
            font-size: 2.5rem;
            color: $principal;
        }
    }
    .btn-new-service{
        display: flex;
        flex-direction: row-reverse;
        margin-right: 5%;
    }
    .service-registered{
        width: 90%;
        margin: auto;
        text-align: center;
        @include grid(4, 1rem);
        span{
            color: $principal;
        }
        border-bottom: 2px solid $principal;
    }
    .actions-services{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        font-size: 1.5rem;
        button{
            height: min-content;
            width: min-content;
            margin: 0;
        }
        .btn-remove{
            margin-left: 2.5%;
        }
    }
    .show-services{
        overflow-y: scroll;
        height: 60vh;
    }
    @include telefono(){
        display: flex;
        flex-direction: column;
        .btn-new-service{
            margin-top: 1rem;
        }
        .title-services{
            display: none;
        }
        .service-registered{
            display: flex;
            flex-direction: column;
            p{
                margin: 0;
            }
            .actions-services{
                margin-bottom: 1rem;
            }
        }
    }
}