.container-left-primary{
    @include degraded-background($principal, $secundario);
    @include grid(2, 0rem);
    h1{
        font-weight: bold;
        font-size: 5rem;
    }
    h3{
        font-size: 3rem;
        font-weight: lighter;
    }
    display: grid;
    .img-container-left {
        grid-row: 1;
        height: max-content;
    }
    .container {
        margin-top: 5rem;
        padding-bottom: 100cqmin;
    }
    .img-container-left {
        grid-column: 2;
        img{
            height: 100vh;
        }
    }
    .container {
        margin: auto;
        padding: 0
    }
}

.container-left-secondary {
    @include degraded-background($secundario, $terciario);
    h1{
        font-weight: bold;
        font-size: 5rem;
    }
    h3{
        font-size: 3rem;
        font-weight: lighter;
    }
    display: grid;
    gap: 0rem;
    .img-container-left {
        grid-row: 1;
        height: max-content;
    }
    .img-container-left {
        grid-column: 2;
        img{
            height: 100vh;
        }
    }
    .div-aux{
        margin: auto;
    }
    @include grid(2, 0rem);
}

.container-left-secondary-register {
    @include degraded-background($secundario, $terciario);
    @include grid(2, 0rem);
    h1{
        font-weight: bold;
        font-size: 5rem;
    }
    h3{
        font-size: 3rem;
        font-weight: lighter;
    }
    .div-aux{
        padding: 2rem 0;
    }
    display: grid;
    gap: 0rem;
    .img-container-left {
        grid-row: 1;
        height: max-content;
    }
    // height: 100vh;
    .container-fields{
        height: 40vh;
        overflow-y: scroll;
    }
    .img-container-left {
        grid-column: 2;
        img{
            height: 100vh;
        }
    }
}

.window-login {
    text-align: center;
    margin: auto;
    width: 80%;
    border-radius: 2rem;
    background-color: $blanco;
    form{
        width: 80%;
        margin: auto;
        display: flex;
        flex-direction: column;
        label{
            text-align: left;
        }
        input{
            width: 100%;
            border-radius: 1rem;
            align-self: center;
            border-color: $terciario;
            margin-bottom: 2rem;
        }
    }
}

.logo-login{
    margin: auto;
    width: 25%;
}

@include telefono(){
    .container-left-primary{
        display: flex;
        flex-direction: column-reverse;
        .img-container-left{
            img{
                height: 30vh;
            }
        }
        .container{
            height: 70vh;
            margin: auto;
            h1{
                padding-top: 3rem;
            }
        }
    }
    .container-left-secondary{
        display: flex;
        flex-direction: column-reverse;
        .img-container-left{
            img{
                height: 30vh;
            }
        }
        .window-login{
            height: 70vh;
            padding-top: 2rem;
        }
    }
    .container-left-secondary-register{
        display: flex;
        flex-direction: column-reverse;
        .img-container-left{
            img{
                height: 30vh;
            }
        }
        .div-aux{
            height: 70vh;
            .window-login{
                height: 65vh;
                .container-fields{
                    height: 25vh;
                }
            }
        }
    }
}