.manager{
    // background-image: url("/img/manager_background.webp");
    height: 100vh;
    // padding: 5rem;
    overflow-y: scroll;
    display: grid;
    grid-template-columns: 2fr 8fr;
    .employees-infos{
        height: 90vh;
        overflow-y: scroll
    }
    .infos{
        @include grid(2, 5rem);
        margin-bottom: 5rem;
        width: 90%;
        margin: auto auto 1rem auto;
        border-bottom: 1px solid $principal;
    }
    .manager-info{
        border-radius: 2rem;
        background-color: rgb(255,255,255,0.4);
        display: grid;
        grid-template-columns: 2fr 5fr 3fr;
        gap: 1rem;
        cursor: pointer;
        .img-manager{
            width: 90%;
            height: min-content;
            margin: auto;
        }
        .description{
            p{
                font-size: 2.4rem;
            }
            span{
                margin-bottom: 0.5rem;
                display: flex;
                flex-direction: row;
                font-size: 1.8rem;
            }
        }
        .stars{
            padding: 1rem;
            text-align: center;
            .star{
                color: white;
                transition: color 0.3s;
            }
            .star.checked{
                color: $principal;
            }
        }
    }
    .last-comment{
        .name-rating{
            display: flex;
            flex-direction: row;
            .stars-comment{
                align-self: center;
                margin-left: 4rem;
                padding: 1rem;
                text-align: center;
                .star-comment{
                    color: white;
                    transition: color 0.3s;
                }
                .star-comment.checked{
                    color: $principal;
                }
            }
        }
        .date-comment{
            p{
                margin: 0;
                font-size: 1.5rem;
                color: $principal;
            }
        }
    }
    .selected{
        p,
        span{
            color: white;
        }
        img{
            filter: invert(100%);
        }
        .stars{
            .star{
                color: transparent;
            }
            .star.checked{
                color: white;
            }
        }
    }
    @include telefono(){
        display: flex;
        flex-direction: column;
        .employees-infos{
            width: 90%;
            margin: auto;
            .infos{
                gap: 0;
                display: flex;
                flex-direction: column;
                margin-bottom: 1rem;
            }
        }
    }
}
.dark-mode{
    .manager{
        span{
            color: white;
        }
        .infos{
            img{
                filter: invert(100%);
            }
        }
    }
}