.client-branchs {
    display: grid;
    grid-template-columns: 2fr 5fr 3fr;
    .map {
        margin-left: 5%;
        width: 90%;
        margin-top: 10vh;
        height: 80vh;
    }
    .location{
        text-align: center;
        margin: auto;
        form{
            display: flex;
            flex-direction: column;
            select{
                padding: 1rem 2rem;
                border-color: $terciario;
                border-radius: 1rem;
                color: black;
                text-align: center;
                font-weight: bold;
            }
            button{
                margin-top: 2rem;
            }
        }
    }
}

@include telefono() {
    .client-branchs{
        display: flex;
        flex-direction: column;
        gap: 1rem;
        @include title-phone();
        .map{
            margin-left: 5%;
            width: 90%;
            margin-top: 5vh;
            height: 50vh;
        }
    }
}