.view_user{
    display: grid;
    grid-template-columns: 2fr 8fr;
    .container-user{
        .back-button{
            margin: 2rem;
        }
        .model-user{
            width: 65%;
            margin: auto;
            @include grid(2, 1rem);
            background-color: $gris;
            border-radius: 1rem;
            p{
                margin: 1rem;
            }
            span{
                color: $principal;
            }
        }
    }
}