.owner-employee{
    display: grid;
    grid-template-columns: 2fr 8fr;
    .title-employee{
        width: 95%;
        margin: auto;
        @include grid(4, 1rem);
        h1{
            font-size: 2.5rem;
            color: $principal;
        }
    }
    .employee{
        width: 95%;
        margin: auto;
        @include grid(4, 1rem);
        border-bottom: 1px solid $principal;
        justify-content: center;
        place-items: center;
        p{
            margin: 0;
        }
    }
    .header-employee{
        .btn-new-employee{
            width: 20%;
            margin: auto 0 auto auto;
        }
    }
    
    .actions-employees{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        font-size: 1.5rem;
        button{
            margin: 1rem 0;
            width: min-content;
            height: min-content;
        }
        .btn-remove{
            margin-left: 2.5%;
        }
    } 
    .add-new-employee{
        width: 90%;
        margin: 0;
    }
    @include telefono(){
        display: flex;
        flex-direction: column;
        .title-employee{
            display: none;
        }
        .employee{
            display: flex;
            p{
                margin: 0;
            }
        }
        .header-employee{
            .btn-new-employee{
                width: 90%;
                margin: auto;
            }
        }
        .modal-content{
            height: 50vh;
            overflow-y: scroll;
        }
    }
}
.modal-form{
    form{
        .scroll-modal-employee{
            height: 40vh;
            overflow-y: scroll;
            margin-bottom: 2rem;
        }
        .branch-checkbox{
            display: flex;
            flex-direction: row-reverse;
            input{
                margin: 0;
                accent-color: $principal;
            }
            label{
                text-align: center;
                width: 50%;
                align-self: center;
            }
        }
    }
}
